

















import { Vue, Component, Prop } from 'vue-property-decorator'
@Component({
  name: 'RoomCard',
  components: {}
})
export default class RoomCard extends Vue {
  @Prop({ required: true })
  private model!: any

  private cNum = 0
  private dialog = false

  private onOpenRoom() {
    this.$router.push({ name: 'host-room', params: { token: this.model.token } })
  }
}
