














import { Vue, Component, Prop } from 'vue-property-decorator'
@Component({
  name: 'AwardStatus'
})
export default class AwardStatus extends Vue {
  @Prop()
  private model!: any

  private startDrawClick() {
    this.$router.push({ name: 'draw', params: { awardId: this.model.objectId } })
  }
}
